<template>
  <div class="container">
    <div class="row">
      <div class="col-12 mt-2">
        <VueSignaturePad
          id="signature"
          width="100%"
          height="500px"
          ref="signaturePad"
          :options="options"
        />
      </div>
    </div>
    <div class="row">
      <div class="col-3 mt-2">
        <compact-picker :value="color" @input="updateVal" />
      </div>
    </div>
  </div>
</template>
<script>
import { Compact } from 'vue-color';

export default {
  name: "Arena",
  components: {
    "compact-picker": Compact
  },
  sockets: {
    connect: () => {
      console.log('Connected')
    }
  },
  data: () => { return {
    color:{
      hex: '#194d33e6',
      hsl: {
        h: 150,
        s: 0.5,
        l: 0.2,
        a: 0.9
      },
      hsv: {
        h: 150,
        s: 0.66,
        v: 0.30,
        a: 0.9
      },
      rgba: {
        r: 159,
        g: 96,
        b: 43,
        a: 0.9
      },
      a: 0.9
    },
    options: {
      penColor: "#000",
      backgroundColor: "#fff",
      onEnd: () => {}
    }
  } },
  methods: {
    updateVal(value) {
      this.options = {
        penColor:value.hex
      }
    }
  },
  created() {
      this.sockets.subscribe('img', (data) => {
         console.log('asd'+data);
         this.$refs.signaturePad.fromDataURL(data)
      })
    this.options.onEnd = () => {
      const { isEmpty, data } = this.$refs.signaturePad.saveSignature();
      if(!isEmpty) {
        //console.log(data);
      }
      this.$socket.emit('draw', data);
      console.info('Drawing!');
    }
  }
};
</script>

<style>
#signature {
  border: double 3px transparent;
  border-radius: 5px;
  background-image: linear-gradient(white, white),
    radial-gradient(circle at top left, #4bc5e8, #9f6274);
  background-origin: border-box;
  background-clip: content-box, border-box;
}
</style>
